.logo {
  display: flex;
  align-items: center;
  gap: 8px;
  @media (max-width: 400px) {
    flex-direction: column;
    gap: 3px;
    padding-left: 10px;
    padding-top: 7px;
  }
  .logo-img {
    @media (max-width: 400px) {
      margin-right: 1px;
      max-width: 29px;
      max-height: 28px;
    }
  }
  .logo-text {
    margin-top: 4px;
    font-family: "Raleway";
    font-style: normal;
    font-weight: 800;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.02em;
    @media (max-width: 400px) {
      text-align: center;
      font-size: 11px;
      line-height: 13px;
    }
  }
  &:hover {
  }
  &:active {
  }
  &:focus {
  }
}
